import React, { useEffect, useState } from 'react';
import Sidebar from '../components/Sidebar';
import { getReservationHistory } from '../services/reservation';

import { Reservation } from '../types/reservation';
import { format } from 'date-fns';
import { toast } from 'react-toastify';
import '../styles/global.css'; // Importando o CSS global

const Historico: React.FC = () => {
  const [reservations, setReservations] = useState<Reservation[]>([]);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        const historyData = await getReservationsHistory();
        setReservations(historyData);
      } catch (error) {
        toast.error('Erro ao carregar o histórico de reservas!');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  return (
    <div className="container">
      <Sidebar />
      <div className="main-content">
        <h1>Histórico de Reservas</h1>

        <div className="reservations-table">
          <h2>Reservas Concluídas</h2>
          {loading ? (
            <p>Carregando histórico...</p>
          ) : (
            <>
              {Array.isArray(reservations) && reservations.length > 0 ? (
                <table>
                  <thead>
                    <tr>
                      <th>Ambiente</th>
                      <th>Data</th>
                      <th>Status</th>
                      <th>Usuário</th>
                    </tr>
                  </thead>
                  <tbody>
                    {reservations.map((reservation) => (
                      <tr key={reservation.id}>
                        <td>{reservation.environment}</td>
                        <td>{format(new Date(reservation.date), 'dd/MM/yyyy HH:mm')}</td>
                        <td>{reservation.status}</td>
                        <td>{reservation.user}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              ) : (
                <p>Não há registros de histórico de reservas.</p>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default Historico;
